.subscribe-wall{
  background-image: url('../images/footer.jpg');
  height: 400px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  .form-control{
      border-radius: 0px;

  }
  .input-email{
      padding-top: 19px;
      padding-bottom: 19px;
      padding-left: 1rem;
      padding-right: 6.5rem;
      background-color: transparent;
      border: 1px solid #ffffff99;
  }
  .btn{
      padding-top: 7px;
      padding-bottom: 7px;
      border-radius: 0px;
  }
}


.footer{
  .navbar-brand{
      padding-top: 32px;
      padding-bottom: 32px;
      padding-right: 16px;
      margin: 0px;
      
  }

  .navbar-expand-lg .navbar-nav .nav-link {
    padding-right: .5rem;
    padding-left: .5rem;
    color: #FFFFFFDE;
}
.navbar-expand-lg .navbar-nav .nav-link:hover{
    color:#0E8A00;;
  }
}
